const mapStateToProps = state => {
    return { menu: state.menuIsOpen }
}

const mapDispatchToProps = dispatch => {
    return {
        toggleMenu: () => {
            dispatch({ type: 'TOGGLE_MENU', open: null });
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(App)

